<template>
  <div class="box">
    <el-table :data="list" style="width: 100%">
      <el-table-column prop="id" label="角色编号" width="180"></el-table-column>
      <el-table-column prop="rolename" label="角色名称" width="180"></el-table-column>

      <el-table-column label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status==1">启用</el-button>
          <el-button type="info" v-else>禁用</el-button>
        </template>
      </el-table-column>

      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="adite(scope.row.id)">编辑</el-button>
          <del-btn @confirm="del(scope.row.id)"></del-btn>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { requestRoleDelete } from "../../../util/request";
import { successAlert, warningAlert } from "../../../util/alert";

import { mapActions, mapGetters } from "vuex";

export default {
  computed: {
    ...mapGetters({
      list: "role/list",
    }),
  },
  components: {},
  data() {
    return {};
  },
  methods: {
    ...mapActions({
      requestList: "role/requestList",
    }),

    adite(id) {
      // console.log(scope)
      this.$emit("edite", id);
    },
    // 删除
    del(id){
          requestRoleDelete({id:id}).then(res=>{
              if(res.data.code==200){
                  successAlert("删除成功")
                  this.requestList()
              }else{
                  warningAlert(res.data.msg)
              }
          })
      }


  },
  mounted() {
    this.requestList();
  },
};
</script>
<style scoped>
</style>


